@use '@angular/material' as mat;

@mixin tei-general-theme($theme) {
  $primary: map-get($theme, primary);
  $accent: map-get($theme, accent);
  $warn: map-get($theme, primary);
  $foreground: map-get($theme, foreground);
  $background: map-get($theme, background);

  .m-tabs .mat-tab-label-active {
    color: mat.get-color-from-palette($primary);
    opacity: 1;
  }

  .o-card-radiobutton .mat-radio-checked .mat-card {
    border: 2px solid mat.get-color-from-palette($accent);
  }

  .a-header-avatar.mat-primary, .avatar-container .avatar-content.mat-primary {
    background-color: mat.get-color-from-palette($primary);
    color: #fff;
  }

  .a-header-avatar.mat-accent, .avatar-container .avatar-content.mat-accent {
    background-color: mat.get-color-from-palette($accent);
    color: #fff;
  }

  .a-header-avatar.add-user, .avatar-container .avatar-content.add-user {
    color: #bec3c9;
    border: 1px dashed #bec3c9;
    
  }

}

