      transform-origin: 50% 50%;
      transform: rotate(-90deg);
    }
    g[data-name='chevron-down'] {
      transform-origin: 50% 50%;
      transform: rotate(180deg);
    }
  }
  nb-menu .menu-group,
  nb-menu .menu-item a {
    font-family: Roboto, 'Helvetica Neue', sans-serif;
    font-size: 0.95rem;
    font-weight: 400;
    padding: 12px;
    line-height: 1;
    &.active {
      font-weight: 600;
    }
  }
  .menu-item a .menu-title {
    margin-left: 16px;
    width: 195px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

.tei-material-theme.nb-theme-material-light {
  .menu-items {
    background-color: #ffffff;
  }

  nb-menu .menu-group,
  nb-menu .menu-item a {
    background-color: #ffffff;
    &.active {
      background-color: #ebebeb;
      &:hover {
        border-color: #028eb5;
      }
    }
  }
  nb-menu .menu-item .active + .expanded {
    background-color: #ebebeb;
    border-left: 2px solid #028eb5;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 1rem;
    .menu-item a {
      background-color: #ebebeb;
      margin-top: 2px;
      &:hover {
        background-color: #0000000a;
        border-radius: 2px;
      }
      &.active {
        background-color: #ffffff;
        border-radius: 2px;
      }
    }
  }
}

.tei-material-theme.nb-theme-material-dark {
  .menu-items {
    background-color: #303030;
  }

  nb-menu .menu-group,
  nb-menu .menu-item a {
    background-color: #303030;
    &.active {
      background-color: #1f1f1f;
      &:hover {
        border-color: #028eb5;
      }
    }
  }
  nb-menu .menu-item .active + .expanded {
    background-color: #1f1f1f;
    border-left: 2px solid #028eb5;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 1rem;
    .menu-item a {
      background-color: #1f1f1f;
      margin-top: 2px;
      &:hover {
        background-color: #242424;
        border-radius: 2px;
      }
      &.active {
        background-color: #303030;
        border-radius: 2px;
      }
    }
  }
}

