.grecaptcha-badge {
  visibility: hidden !important;
}
p {
  margin-bottom: 0;
}

.tei-material-theme
  .permission-table-setting-row
  .mat-form-field.no-label.mat-form-field-appearance-outline
  .mat-form-field-wrapper {
  margin: 0.25em;
}

@include media-breakpoint-up(xl) {
  .o-boxed-container {
    max-width: 1140px;
  }
}

@include media-breakpoint-only(lg) {
  .o-boxed-container {
    max-width: 960px;
  }
}

@include media-breakpoint-only(md) {
  .o-boxed-container {
    max-width: 720px;
  }
}

@include media-breakpoint-only(sm) {
  .o-boxed-container {
    max-width: 540px;
  }
}

@include media-breakpoint-down(xs) {
  .o-boxed-container {
    width: 100%;
    padding-right: 0.75rem;
    padding-left: 0.75rem;
  }
}

// Angular 15 Material UI fix
.tei-material-theme {
  .mat-card .mat-divider-horizontal {
    position: absolute;
    left: 0;
    width: 100%;
  }
}

.tei-material-theme.nb-theme-material-dark .mat-stroked-button:disabled {
  color: rgba(255, 255, 255, 0.12) !important;
}

.tei-material-theme.nb-theme-material-light .mat-stroked-button:disabled {
  color: rgba(0, 0, 0, 0.12) !important;
}

.a-icon-border {
  padding: 0;
  min-width: 40px;
  min-height: 40px;
  border-radius: 4px !important;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

.nb-theme-material-light {
  .m-table-header-boder {
    background-color: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  }
}

.nb-theme-material-dark {
  .m-table-header-boder {
    background-color: #303030;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  }
}

.m-table-container {
  overflow-x: hidden;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    margin-right: 2px;
  }
}

.o-table-header-spaces {
  padding: 0.5rem;
}

.o-card-table {
  padding: 0 !important;
  overflow: hidden;
  display: flex !important;
  flex-direction: column;
  justify-content: flex-start;

  .m-filters-chips {
    padding: 0.25rem 0;
  }
}

.o-card-table-fixed-height .m-table-container {
  flex-grow: 1;
}

@include media-breakpoint-down(xs) {
  .mat-paginator-page-size-label {
    display: none;
  }
}

#m-filters-search {
  @include media-breakpoint-down(sm) {
        .mat-form-field {
      width: 100%;
    }
    & {
      margin-right: 0;
    }
  }

  height: 44px;
  font-size: 13px;
  margin-right: 0.25rem;

  .mat-form-field-wrapper {
    padding-bottom: 0px;
    padding-top: 0px;
    margin: 0;
  }

  .mat-form-field-flex {
    height: 44px;
    min-height: 44px;
    align-items: center;
    margin: 0;
  }

  .mat-input-element {
    padding: 0 0 8px 0;
    font-size: 13px;
  }

  .mat-form-field-outline {
    top: 0px !important;
  }

  .mat-form-field-outline-start,
  .mat-form-field-outline-end {
    top: 0px !important;
  }

  .mat-form-field-infix {
    padding: 0 8px;
  }
}

// Empty State
.a-table-empty {
  justify-content: center;
  margin: 1.5rem 0;
  display: flex;
  width: 100%;
  padding: 0 0.5rem;
  text-align: center;
}

.a-table-pagination {
  padding-right: 0.5rem;
  flex-shrink: 1;
}

// Responsive Design
@include media-breakpoint-down(sm) {
  .a-table-pagination {
    .mat-paginator-page-size-label {
      display: none;
    }
  }
  .m-table-container .responsive-header-row {
    display: none;
  }
}

// Theme Variations
.nb-theme-material-light {
  .o-table-header-border {
    background-color: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  }
  .a-table-container {
    &-scroll {
      background-color: rgba(0, 0, 0, 0.12);
    }
  }

  .a-table-row,
  .a-table-header-row {
    background-color: #fff;
  }

  .a-table-pagination {
    border-top: 1px solid rgba(0, 0, 0, 0.12);
  }

  .a-table-mobile-row {
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    background-color: #fff;
  }
}

.nb-theme-material-dark {
  .o-table-header-border {
    background-color: #303030;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
  }

  .a-table-container-scroll {
    background-color: rgba(255, 255, 255, 0.12);
  }

  .a-table-row,
  .a-table-header-row {
    background-color: #303030;
  }

  .a-table-pagination {
    border-top: 1px solid rgba(255, 255, 255, 0.12);
  }

  .a-table-mobile-row {
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    background-color: #303030;
  }

  .a-table-empty {
    color: rgba(255, 255, 255, 1);
  }
}

// Truncate text mixin
@mixin truncate-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Table Text Styles
.a-table-text {
  &-link {
    color: #028eb5;
    cursor: pointer;
  }
}

// Table Layout
.a-table-row,
.a-table-header-row {
  display: flex;
  column-gap: 20px;
  align-items: center;
}

.a-table-cell {
  &-truncate {
    @include truncate-text;
  }

  &-fixed-width {
    max-width: 150px;
    @include truncate-text;
  }
}

// Selection States
.a-table-selected-row {
  background-color: #097d9e !important;

  * {
    color: #fff !important;
  }

  .a-text-primary {
    color: rgba(255, 255, 255, 1);
  }

  .a-text-secondary {
    color: rgba(255, 255, 255, 0.9);
  }
}
