@use '@angular/material' as mat;
:root {
  --tei-light-primary-color: #244c5a;
  --tei-light-primary-color-50: #244c5a;
  --tei-light-primary-color-100: #244c5a;
  --tei-light-primary-color-200: #244c5a;
  --tei-light-primary-color-300: #244c5a;
  --tei-light-primary-color-400: #244c5a;
  --tei-light-primary-color-500: #244c5a;
  --tei-light-primary-color-600: #244c5a;
  --tei-light-primary-color-700: #244c5a;
  --tei-light-primary-color-800: #244c5a;
  --tei-light-primary-color-900: #244c5a;
  --tei-light-primary-color-A100: #244c5a;
  --tei-light-primary-color-A200: #244c5a;
  --tei-light-primary-color-A400: #244c5a;
  --tei-light-primary-color-A700: #244c5a;

  --tei-light-primary-contrast-color: #ffffff;
}

$tei-light-primary-color: var(--tei-light-primary-color);
$tei-light-primary-color-50: var(--tei-light-primary-color-50);
$tei-light-primary-color-100: var(--tei-light-primary-color-100);
$tei-light-primary-color-200: var(--tei-light-primary-color-200);
$tei-light-primary-color-300: var(--tei-light-primary-color-300);
$tei-light-primary-color-400: var(--tei-light-primary-color-400);
$tei-light-primary-color-500: var(--tei-light-primary-color-500);
$tei-light-primary-color-600: var(--tei-light-primary-color-600);
$tei-light-primary-color-700: var(--tei-light-primary-color-700);
$tei-light-primary-color-800: var(--tei-light-primary-color-800);
$tei-light-primary-color-900: var(--tei-light-primary-color-900);
$tei-light-primary-color-A100: var(--tei-light-primary-color-A100);
$tei-light-primary-color-A200: var(--tei-light-primary-color-A200);
$tei-light-primary-color-A400: var(--tei-light-primary-color-A400);
$tei-light-primary-color-A700: var(--tei-light-primary-color-A700);

$tei-light-primary-contrast-color: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-50: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-100: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-200: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-300: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-400: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-500: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-600: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-700: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-800: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-900: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-A100: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-A200: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-A400: var(--tei-light-primary-contrast-color);
$tei-light-primary-contrast-color-A700: var(--tei-light-primary-contrast-color);

$tei-light-primary-color-palette: (
  50: $tei-light-primary-color-50,
  100: $tei-light-primary-color-100,
  200: $tei-light-primary-color-200,
  300: $tei-light-primary-color-300,
  400: $tei-light-primary-color-400,
  500: $tei-light-primary-color-500,
  600: $tei-light-primary-color-600,
  700: $tei-light-primary-color-700,
  800: $tei-light-primary-color-800,
  900: $tei-light-primary-color-900,
  A100: $tei-light-primary-color-A100,
  A200: $tei-light-primary-color-A200,
  A400: $tei-light-primary-color-A400,
  A700: $tei-light-primary-color-A700,
  contrast: (
    50: $tei-light-primary-contrast-color-50,
    100: $tei-light-primary-contrast-color-100,
    200: $tei-light-primary-contrast-color-200,
    300: $tei-light-primary-contrast-color-300,
    400: $tei-light-primary-contrast-color-400,
    500: $tei-light-primary-contrast-color-500,
    600: $tei-light-primary-contrast-color-600,
    700: $tei-light-primary-contrast-color-700,
    800: $tei-light-primary-contrast-color-800,
    900: $tei-light-primary-contrast-color-900,
    A100: $tei-light-primary-contrast-color-A100,
    A200: $tei-light-primary-contrast-color-A200,
    A400: $tei-light-primary-contrast-color-A400,
    A700: $tei-light-primary-contrast-color-A700
  )
);

$tei-light-primary: mat.define-palette($tei-light-primary-color-palette, 500);
