@include media-breakpoint-up(xs) {
    .md-drppicker.double .calendar.left {
        float: none !important;
    }
    }

@include media-breakpoint-up(sm) {

}

@include media-breakpoint-up(md) {
.md-drppicker.double {
    min-width: 600px;
}
.md-drppicker.double .calendar.left {
    float: left !important;
}
}

.mat-form-field .md-drppicker .ranges ul li button.active, 
.mat-form-field .md-drppicker td.active, 
.mat-form-field .md-drppicker td.active:hover {
    background-color: var(--tei-light-primary-color);
}

.mat-form-field .md-drppicker .btn, .mat-form-field .md-drppicker .btn:hover {
    background-color: var(--tei-light-accent-color);
}

.mat-form-field .md-drppicker .clear {
    display: none;
}