@use '@angular/material' as mat;

@mixin tei-custom-loading-bar-component-theme($theme) {
  $primary: map-get($theme, primary);

  ngx-loading-bar {
    .custom-loading-bar-spinner-icon {
      border-left-color: rgba($color: #000000, $alpha: 0.15);
      border-right-color: rgba($color: #000000, $alpha: 0.15);
      border-bottom-color: rgba($color: #000000, $alpha: 0.15);
      border-top-color: mat.get-color-from-palette($primary);
    }
  }
}
