@use "@angular/material" as mat;
@mixin angular-material() {
  // TODO(v15): As of v15 mat.legacy-core no longer includes default typography styles.
  //  The following line adds:
  //    1. Default typography styles for all components
  //    2. Styles for typography hierarchy classes (e.g. .mat-headline-1)
  //  If you specify typography styles for the components you use elsewhere, you should delete this line.
  //  If you don't need the default component typographies but still want the hierarchy styles,
  //  you can delete this line and instead use:
  //    `@include mat.legacy-typography-hierarchy(mat.define-legacy-typography-config());`
  // @include mat.all-legacy-component-typographies();
  // @include mat.legacy-core();

  @include nb-for-theme(material-dark) {
    // Define a dark theme
    $dark-theme: mat.define-dark-theme(
      (
        color: (
          primary: mat.define-palette(mat.$pink-palette),
          accent: mat.define-palette(mat.$blue-grey-palette)
        ),
        // Only include `typography` and `density` in the default dark theme.
        typography: mat.define-legacy-typography-config(),
        density: 0
      )
    );
    @include mat.all-legacy-component-themes($dark-theme);
    @include include-mdc-overrides();
  }

  @include nb-for-theme(material-light) {
    // Define a light theme
    $light-theme: mat.define-light-theme(
      (
        color: (
          primary: mat.define-palette(mat.$indigo-palette),
          accent: mat.define-palette(mat.$pink-palette)
        )
      )
    );
    @include mat.all-legacy-component-colors($light-theme);
    @include include-mdc-overrides();
  }
}

//webpack:///node_modules/@angular/material/prebuilt-themes/indigo-pink.css

@mixin include-mdc-overrides() {
  @include generic-mdc-override();
  @include slide-toggle-mdc-overrides();
  @include table-mdc-overrides();
  @include checkbox-mdc-overrides();
  @include form-field-mdc-overrides();
  @include card-mdc-overrides();
  @include button-mdc-overrides();
  @include radio-button-mdc-overrides();
  @include select-mdc-overrides();
  @include chip-mdc-overrides();
  @include dialog-mdc-overrides();
  @include tooltip-mdc-overrides();
  @include snack-bar-mdc-overrides();
  @include tab-mdc-overrides();
}

@mixin generic-mdc-override() {
  --mdc-typography-font-family: Roboto, "Helvetica Neue", sans-serif;
  --mdc-typography-body2-letter-spacing: 0;
  --mdc-theme-text-primary-on-background: #{nb-theme(text-basic-color)};
  --mdc-typography-caption-font-size: 75%;
  --mdc-typography-body1-font-size: 14px;
  --mdc-typography-body1-letter-spacing: 0;
  --mdc-theme-error: #{nb-theme(color-danger-450)};
  --mdc-theme-primary: #{nb-theme(color-primary-default)};
  --mdc-theme-surface: #{nb-theme(background-surfer-color)};
  --mdc-theme-on-surface: #{nb-theme(text-basic-color)};
}

@mixin tab-mdc-overrides() {
  .mdc-tab {
    --mdc-typography-button-letter-spacing: 0;
    --mdc-tab-indicator-active-indicator-color: #{nb-theme(color-primary-default)};
    font-weight: 700;
  }

  .mat-mdc-tab-group.mat-mdc-tab-group-stretch-tabs > .mat-mdc-tab-header .mat-mdc-tab {
    flex-grow: 0;
    min-width: 160px;
  }
  .mat-mdc-tab .mdc-tab__text-label,
  .mat-mdc-tab-link .mdc-tab__text-label {
    color: #{nb-theme(text-basic-color)};
    opacity: 0.6;
  }
  .mat-mdc-tab:not(.mat-mdc-tab-disabled).mdc-tab--active .mdc-tab__text-label,
  .mat-mdc-tab-link:not(.mat-mdc-tab-disabled).mdc-tab--active .mdc-tab__text-label {
    color: #{nb-theme(color-primary-default)};
    opacity: 1;
  }

  .mdc-tab__ripple::before,
  .mat-mdc-tab .mat-ripple-element,
  .mat-mdc-tab-header-pagination .mat-ripple-element,
  .mat-mdc-tab-link .mat-ripple-element {
    background-color: #{nb-theme(color-primary-default)};
  }
}

@mixin dialog-mdc-overrides() {
  .mat-mdc-dialog-container {
    --mdc-dialog-container-color: #{nb-theme(dialog-background-color)};
    --mdc-dialog-with-divider-divider-color: rgba(0, 0, 0, 0.12);
    --mdc-dialog-subhead-color: rgba(0, 0, 0, 0.87);
    --mdc-dialog-supporting-text-color: rgba(0, 0, 0, 0.6);

    .o-dialog-container {
      margin: 0;
    }

    .m-dialog-actions.a-button-contained {
      padding: 0;
      text-align: center;

      .mat-mdc-button {
        height: 48px;
        width: 100%;
        border-radius: 0 0 4px 4px;
        font-size: 16px;
      }
    }
  }
}

@mixin chip-mdc-overrides() {
  .mat-mdc-standard-chip {
    --mdc-chip-elevated-container-color: #{nb-theme(color-primary-default)};
    --mdc-chip-label-text-color: #{nb-theme(color-basic-100)};
    --mdc-chip-with-trailing-icon-trailing-icon-color: #{nb-theme(color-basic-100)};
    .mdc-evolution-chip__text-label {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@mixin select-mdc-overrides() {
  .mat-mdc-select {
    .mat-mdc-select-value {
      color: #{nb-theme(text-basic-color)};
    }

    .mat-mdc-select-arrow svg {
      fill: #{nb-theme(text-basic-color)};
    }
  }
  .mat-mdc-option {
    .mat-pseudo-checkbox-minimal {
      display: none;
    }
  }
}

@mixin radio-button-mdc-overrides() {
  .mat-mdc-radio-button {
    &.mat-primary {
      --mdc-radio-disabled-selected-icon-color: #000;
      --mdc-radio-disabled-unselected-icon-color: #000;
      --mdc-radio-unselected-focus-icon-color: #212121;
      --mdc-radio-unselected-hover-icon-color: #212121;
      --mdc-radio-unselected-icon-color: #{nb-theme(radio-button-outer-circle-color)};
      --mdc-radio-unselected-pressed-icon-color: rgba(0, 0, 0, 0.54);
      --mdc-radio-selected-focus-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-hover-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-pressed-icon-color: #{nb-theme(color-primary-default)};
      --mat-mdc-radio-ripple-color: #000;
      --mat-mdc-radio-checked-ripple-color: #{nb-theme(color-primary-default)};
    }

    &.mat-accent {
      --mdc-radio-disabled-selected-icon-color: #000;
      --mdc-radio-disabled-unselected-icon-color: #000;
      --mdc-radio-unselected-focus-icon-color: #212121;
      --mdc-radio-unselected-hover-icon-color: #212121;
      --mdc-radio-unselected-icon-color: #{nb-theme(radio-button-outer-circle-color)};
      --mdc-radio-unselected-pressed-icon-color: rgba(0, 0, 0, 0.54);
      --mdc-radio-selected-focus-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-hover-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-icon-color: #{nb-theme(color-primary-default)};
      --mdc-radio-selected-pressed-icon-color: #{nb-theme(color-primary-default)};
      --mat-mdc-radio-ripple-color: #000;
      --mat-mdc-radio-checked-ripple-color: #{nb-theme(color-primary-default)};

      --mdc-radio-checked-ink-color: #{nb-theme(color-primary-default)};
      --mdc-radio-checked-fill-color: #{nb-theme(color-primary-default)};
      --mdc-radio-checked-hover-fill-color: #{nb-theme(color-primary-default)};
      --mdc-radio-checked-focus-fill-color: #{nb-theme(color-primary-default)};
      --mdc-radio-checked-pressed-fill-color: #{nb-theme(color-primary-default)};
      --mdc-radio-unchecked-hover-border-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-radio-unchecked-focus-border-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-radio-unchecked-pressed-border-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-radio-disabled-checked-fill-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-radio-disabled-unchecked-border-color: #{nb-theme(checkbox-outline-basic-border-color)};
    }

    &.mat-mdc-radio-button-disabled label {
      opacity: 0.38;
    }

    .mdc-form-field > .mdc-radio {
      padding-left: 0px;
    }
  }
}

@mixin button-mdc-overrides() {
  .mdc-button {
    --mdc-typography-button-letter-spacing: 0;
  }
  .mat-mdc-button {
    --mdc-text-button-label-text-color: #{nb-theme(text-basic-color)};
    .mat-icon {
      width: 24px;
      height: 24px;
      font-size: 24px;
      margin-right: 0;
    }
  }

  .mat-mdc-outlined-button {
    --mdc-outlined-button-outline-color: #{nb-theme(disabled-button-color)};
    --mdc-outlined-button-label-text-color: #{nb-theme(color-basic-100)};

    &.mat-primary {
      --mdc-outlined-button-outline-color: #{nb-theme(disabled-button-color)};
      --mdc-outlined-button-label-text-color: #{nb-theme(color-primary-default)};
    }

    &[disabled][disabled] {
      --mdc-outlined-button-label-text-color: #{nb-theme(disabled-button-color)};
      --mdc-outlined-button-disabled-label-text-color: #{nb-theme(disabled-button-color)};
      --mdc-outlined-button-outline-color: #{nb-theme(disabled-button-color)};
      --mdc-outlined-button-disabled-outline-color: #{nb-theme(disabled-button-color)};
    }
  }

  .mat-mdc-raised-button,
  .mat-mdc-unelevated-button {
    &.mat-primary {
      --mdc-protected-button-container-color: #{nb-theme(color-primary-default)};
      --mdc-filled-button-container-color: #{nb-theme(color-primary-default)};
      &[disabled][disabled] {
        --mdc-protected-button-disabled-label-text-color: #{nb-theme(color-basic-100)};
        --mdc-protected-button-disabled-container-color: #{nb-theme(color-primary-default)};
        opacity: 0.4;
      }
    }

    &.mat-accent {
      --mdc-protected-button-container-color: #{nb-theme(color-success-default)};
      --mdc-filled-button-container-color: #{nb-theme(color-success-default)};
      &[disabled][disabled] {
        --mdc-protected-button-disabled-label-text-color: #{nb-theme(color-basic-100)};
        --mdc-protected-button-disabled-container-color: #{nb-theme(color-success-default)};
        opacity: 0.4;
      }
    }

    &.a-button-lg {
      font-size: 16px;
      height: 42px;
    }
  }
}

@mixin form-field-mdc-overrides() {
  .mat-mdc-form-field {
    .mdc-text-field--outlined {
      padding: 0 0.75em 0 0.75em;
      .mdc-notched-outline--upgraded {
        .mdc-floating-label--float-above {
          font-size: 14px;
          --mat-mdc-form-field-label-transform: translateY(-30.75px)
            scale(var(--mat-mdc-form-field-floating-label-scale, 0.75));
          transform: var(--mat-mdc-form-field-label-transform);
        }
      }
      .mdc-notched-outline__leading {
        width: 8px;
      }
      .mdc-notched-outline {
        &.mdc-notched-outline--upgraded {
          height: 51px;
        }

        .mdc-notched-outline__leading,
        .mdc-notched-outline__notch,
        .mdc-notched-outline__trailing {
          border-width: 2px;
        }
      }
    }

    &.no-label {
      .mdc-notched-outline {
        height: 44px;
      }

      .mat-mdc-form-field-infix {
        min-height: 44px;
        max-height: 44px;
        padding-top: 12px;
        padding-bottom: 12px;
      }
    }

    .mat-mdc-form-field-infix {
      min-height: 51px;
      // max-height: 51px;
    }
    .mat-mdc-text-field-wrapper {
      margin: 0.4em 0 0 0;
      .mat-mdc-form-field-flex .mat-mdc-floating-label {
        top: 24px;
      }
    }

    .mat-mdc-form-field-error-wrapper {
      padding: 0 8px;
    }

    .mdc-text-field--focused:not(.mdc-text-field--disabled):not(.mdc-text-field--invalid) .mdc-floating-label {
      color: #{nb-theme(color-primary-default)};
    }
    .mdc-text-field--outlined:not(.mdc-text-field--disabled) .mdc-notched-outline__leading,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled) .mdc-notched-outline__notch,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled) .mdc-notched-outline__trailing {
      border-color: #{nb-theme(field-border-color)};
    }

    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__leading,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__notch,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__trailing {
      border-color: #{nb-theme(field-border-color-hover)};
    }

    .mdc-text-field--invalid:not(.mdc-text-field--disabled) .mdc-notched-outline__leading,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled) .mdc-notched-outline__notch,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled) .mdc-notched-outline__trailing {
      border-color: #{nb-theme(color-danger-450)};
    }

    .mdc-text-field--invalid:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__leading,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__notch,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled):not(.mdc-text-field--focused):hover
      .mdc-notched-outline
      .mdc-notched-outline__trailing {
      border-color: #{nb-theme(color-danger-450)};
    }

    .mdc-text-field--invalid:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__leading,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__notch,
    .mdc-text-field--invalid:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-notched-outline__trailing {
      border-color: #{nb-theme(color-danger-450)};
    }

    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--invalid).mdc-text-field--focused
      .mdc-notched-outline__leading,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--invalid).mdc-text-field--focused
      .mdc-notched-outline__notch,
    .mdc-text-field--outlined:not(.mdc-text-field--disabled):not(.mdc-text-field--invalid).mdc-text-field--focused
      .mdc-notched-outline__trailing {
      border-color: #{nb-theme(color-primary-default)};
    }

    .mdc-text-field:not(.mdc-text-field--disabled) .mdc-floating-label {
      color: #{nb-theme(text-basic-color)};
    }

    .mdc-text-field:not(.mdc-text-field--disabled) .mdc-text-field__input {
      color: #{nb-theme(text-basic-color)};
    }

    .mdc-text-field--invalid:not(.mdc-text-field--disabled) .mdc-floating-label {
      color: #{nb-theme(color-danger-450)};
    }

    .mdc-text-field:not(.mdc-text-field--disabled) .mdc-text-field__input::placeholder {
      color: #{nb-theme(input-place-holder)};
    }

    .mdc-text-field:not(.mdc-text-field--disabled) .mdc-text-field__input:-ms-input-placeholder {
      color: #{nb-theme(input-place-holder)};
    }
  }
}

@mixin card-mdc-overrides() {
  .mat-mdc-card {
    --mdc-elevated-card-container-color: #{nb-theme(card-background-color)};
  }
}

@mixin table-mdc-overrides() {
  mat-header-cell.mat-mdc-header-cell {
    font-size: 12px;
    color: #{nb-theme(table-header-cell-color)};
  }

  mat-table.mat-mdc-table {
    background: #{nb-theme(table-background-color)};
  }

  mat-row.mat-mdc-row,
  mat-header-row.mat-mdc-header-row,
  mat-footer-row.mat-mdc-footer-row {
    border-bottom: 1px solid;
    border-bottom-color: #{nb-theme(table-row-border)};
  }

  .mdc-data-table__cell,
  .mdc-data-table__header-cell {
    border-bottom: none;
  }
}

@mixin checkbox-mdc-overrides() {
  .mat-mdc-checkbox {
    --mdc-checkbox-state-layer-size: 16px;

    &.mat-primary,
    &.mat-accent {
      // --mdc-checkbox-selected-checkmark-color: #fff;
      --mdc-checkbox-selected-focus-icon-color: #{nb-theme(color-primary-default)};
      --mdc-checkbox-selected-hover-icon-color: #{nb-theme(color-primary-default)};
      --mdc-checkbox-selected-icon-color: #{nb-theme(color-primary-default)};
      --mdc-checkbox-selected-pressed-icon-color: #{nb-theme(color-primary-default)};
      --mdc-checkbox-unselected-focus-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-checkbox-unselected-hover-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-checkbox-disabled-selected-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-checkbox-disabled-unselected-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-checkbox-unselected-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
      --mdc-checkbox-unselected-pressed-icon-color: #{nb-theme(checkbox-outline-basic-border-color)};
    }

    &.mat-mdc-checkbox-disabled label {
      opacity: 0.38;
    }

    .mdc-form-field > label {
      padding-left: 8px;
    }

    .mdc-checkbox__ripple {
      background: transparent;
    }

    .mdc-checkbox--selected ~ .mdc-checkbox__ripple {
      background: transparent;
    }

    .mdc-checkbox__background {
      width: 16px;
      height: 16px;
    }
  }
}

@mixin slide-toggle-mdc-overrides() {
  .mat-mdc-slide-toggle {
    &.mat-primary,
    &.mat-accent {
      --mdc-switch-selected-focus-state-layer-color: #{nb-theme(color-primary-default)};
      --mdc-switch-selected-hover-state-layer-color: #{nb-theme(color-primary-default)};
      --mdc-switch-selected-pressed-state-layer-color: #{nb-theme(color-primary-default)};

      --mdc-switch-selected-handle-color: #{nb-theme(color-primary-default)};
      --mdc-switch-selected-focus-handle-color: #{nb-theme(color-primary-default)};
      --mdc-switch-selected-hover-handle-color: #{nb-theme(color-primary-default)};
      --mdc-switch-selected-pressed-handle-color: #{nb-theme(color-primary-default)};

      --mdc-switch-selected-focus-track-color: #{nb-theme(color-primary-transparent-600)};
      --mdc-switch-selected-hover-track-color: #{nb-theme(color-primary-transparent-600)};
      --mdc-switch-selected-pressed-track-color: #{nb-theme(color-primary-transparent-600)};
      --mdc-switch-selected-track-color: #{nb-theme(color-primary-transparent-600)};

