      --mdc-switch-unselected-hover-handle-color: #{nb-theme(slide-toggle-handle-color)};
      --mdc-switch-unselected-pressed-handle-color: #{nb-theme(slide-toggle-handle-color)};
      --mdc-switch-unselected-handle-color: #{nb-theme(slide-toggle-handle-color)};

      --mdc-switch-unselected-focus-track-color: #{nb-theme(slide-toggle-track-color)};
      --mdc-switch-unselected-hover-track-color: #{nb-theme(slide-toggle-track-color)};
      --mdc-switch-unselected-pressed-track-color: #{nb-theme(slide-toggle-track-color)};
      --mdc-switch-unselected-track-color: #{nb-theme(slide-toggle-track-color)};

      --mdc-switch-disabled-selected-handle-color: #{nb-theme(color-primary-default)};
      --mdc-switch-disabled-unselected-handle-color: #{nb-theme(slide-toggle-handle-color)};
      --mdc-switch-disabled-selected-track-color: #{nb-theme(color-primary-transparent-600)};
      --mdc-switch-disabled-unselected-track-color: #{nb-theme(slide-toggle-track-color)};

      --mdc-switch-disabled-handle-elevation:
        0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
      --mdc-switch-disabled-handle-opacity: 0.38;
      --mdc-switch-disabled-track-opacity: 0.38;

      --mdc-switch-handle-surface-color: #{nb-theme(slide-toggle-surface-color)};

      //--mdc-switch-unselected-focus-state-layer-color: #424242;
      //--mdc-switch-unselected-pressed-state-layer-color: #424242;
      //--mdc-switch-unselected-hover-state-layer-color: #424242;
