      //--mdc-switch-disabled-unselected-icon-color: #fff; //--mdc-switch-unselected-icon-color: #fff
    }
  }

  .mat-mdc-slide-toggle {
    .mdc-switch__icons {
      display: none;
    }

    .mdc-switch--disabled + label {
      opacity: 0.38;
    }

    .mdc-form-field > label {
      padding-left: 8px;
