@mixin snack-bar-mdc-overrides() {
  .mat-mdc-snack-bar-container,
  .mat-mdc-snack-bar-container .mdc-snackbar__label {
    --mat-mdc-snack-bar-button-color: rgba(255, 255, 255, 0.7);
    --mdc-snackbar-container-color: #{nb-theme(snackbar-container-color)};
    --mdc-snackbar-supporting-text-color: #{nb-theme(snackbar-supporting-text-color)};
    --mdc-snackbar-supporting-text-size: 16px;
    --mdc-snackbar-supporting-text-weight: 400;
  }

  .mat-mdc-snack-bar-container {
    margin: 24px;
    .mat-mdc-snack-bar-label {
      padding: 14px 16px;
    }
  }
}
